<template>
  <div>
    <el-form class="account-formitem" label-position="left" :model="aliyunForm" ref="appForm"
             label-width="130px"
             style="text-align: left;font-size:20px;font-weight: bold;margin: 30px 30px;position: relative;min-height: 500px">
      <el-row :md="24">
        <el-col :md="12">
          <el-form-item label="key:" prop="key">
            <el-input style="width: 300px" v-model="aliyunForm.key"></el-input>
          </el-form-item>
        </el-col>
        <el-col :md="12">
          <el-form-item label="secret:" prop="secret">
            <el-input style="width: 300px" v-model="aliyunForm.secret"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :md="24">
        <el-col :md="12">
          <el-form-item label="code1:" prop="code1">
            <el-input style="width: 300px" v-model="aliyunForm.code1"></el-input>
          </el-form-item>
        </el-col>
        <el-col :md="12">
          <el-form-item label="code2:" prop="code2">
            <el-input style="width: 300px" v-model="aliyunForm.code2"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :md="24">
        <el-col :md="12">
          <el-form-item label="sign:" prop="sign">
            <el-input style="width: 300px" v-model="aliyunForm.sign"></el-input>
          </el-form-item>
        </el-col>

      </el-row>
      <div style="position: absolute;top:60%;left: 40%">
        <el-button type="primary" @click="aliyun_submit()">提 交</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "AliCloudSmsPlatform",
  data() {
    return {
      aliyunForm: {
        key: '',
        secret: '',
        code1: '',
        code2: '',
        sign: ''
      }
    }
  },
  created() {
    this.aliyun_select();
  },
  methods: {
    //阿里云提交
    aliyun_submit() {
      let that = this;
      this.$confirm('确定要提交么?', '提交提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString: true,
        closeOnClickModal: false,
        closeOnPressEscape: false,
      }).then(() => {
        let _jsondata_ = {
          method: '2',
          value: {
            key: that.aliyunForm.key,
            secret: that.aliyunForm.secret,
            code1: that.aliyunForm.code1,
            code2: that.aliyunForm.code2,
            sign: that.aliyunForm.sign,
          },

        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var s3 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.aliyun_configuration,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
        );
        s3.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
            this.$message({
              type: 'success',
              showClose: true,
              duration: 3000,
              dangerouslyUseHTMLString: true,
              message: '<span style="color:black;">阿里云短信配置成功 </span>'
            });
            this.aliyun_select();
          } else {
            console.info("阿里云短信配置失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({path: '/login'});
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '阿里云短信配置出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '阿里云短信配置出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消保存'
        });
      });
    },
    //阿里云查询
    aliyun_select(){
      let that = this;
      let _jsondata_ = {
        method:'1',
        value: '{}'
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var d2 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.aliyun_configuration,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      d2.then((res) => {
        if (res.data.status == 'success') {
          loading.close();
          if (res.data.msg!==''){
            that.aliyunForm = JSON.parse(res.data.msg)
            console.log(that.aliyunForm)
          }
        } else {
          console.info("查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
  }
}
</script>

<style scoped>
.account-formitem /deep/ .el-form-item {
  margin-bottom: 17px !important;
}
</style>



